.container {
  display: flexbox;
  flex-direction: column;
  align-items: center;
  margin: 20px 0;
}

.card {
  margin: 0px 4% !important;
}

.infected {
  border-bottom: 10px solid rgba(0, 0, 255, 0.918);
}

.recovered {
  border-bottom: 10px solid rgba(0, 255, 0, 0.938);
}

.deaths {
  border-bottom: 10px solid red;
}

@media only screen and (max-width: 770px) {
  .card {
    margin: 2% 0 !important;
  }
}

.boxCss {
  width: 95%;
}
